
















import { Typography } from '@material-ui/core';
import {
  EntityTable,
  useEntity,
  useRelatedEntities,
} from '@backstage/plugin-catalog-react';
import React from 'react';
import {
  InfoCard,

  Link,
  Progress,
  ResponseErrorPanel,

} from '@backstage/core-components';

/** @public */











/**
 * A low level card component that can be used as a building block for more
 * specific cards.
 *
 * @remarks
 *
 * You probably want to make a dedicated component for your needs, which renders
 * this card as its implementation with some of the props set to the appropriate
 * values.
 *
 * @public
 */
export function RelatedEntitiesCard(
  props,
) {
  const {
    variant = 'gridItem',
    title,
    columns,
    entityKind,
    relationType,
    emptyMessage,
    emptyHelpLink,
    asRenderableEntities,
  } = props;

  const { entity } = useEntity();
  const { entities, loading, error } = useRelatedEntities(entity, {
    type: relationType,
    kind: entityKind,
  });

  if (loading) {
    return (
      React.createElement(InfoCard, { variant: variant, title: title,}
        , React.createElement(Progress, null )
      )
    );
  }

  if (error) {
    return (
      React.createElement(InfoCard, { variant: variant, title: title,}
        , React.createElement(ResponseErrorPanel, { error: error,} )
      )
    );
  }

  return (
    React.createElement(EntityTable, {
      title: title,
      variant: variant,
      emptyContent: 
        React.createElement('div', { style: { textAlign: 'center' },}
          , React.createElement(Typography, { variant: "body1",}, emptyMessage)
          , React.createElement(Typography, { variant: "body2",}
            , React.createElement(Link, { to: emptyHelpLink,}, "Learn how to change this."    )
          )
        )
      ,
      columns: columns,
      entities: asRenderableEntities(entities || []),}
    )
  );
}
